<template>
	<div v-if="loading">
		<div style="position: relative; top: 100px; -webkit-box-align:center; -webkit-box-pack:center; display:-webkit-box; font-size:54px">
			 <img src="../../assets/img/loading.gif">
		</div>
	</div>
	
	<div v-else>
		<form action="/" class="payment-form payment-form--create d-flex justify-content-stretch">				
			<fieldset class="sender-data form-section-wrapper">
				<legend></legend>
				
				<div class="form-section">
					<div class="form-group">
						<label for="senderSurname"></label>
						<input type="text" class="form-control" id="senderSurname" placeholder=" " v-model="id">
						<div class="invalid-feedback">
							 ,    .
						</div>
					</div>
					<div class="form-group">
						<label for="senderName">'</label>
						<input type="text" class="form-control" id="senderName" placeholder="' " v-model="name">
						<div class="invalid-feedback">
							 ,   ' .
						</div>
					</div>
					<div class="form-group">
						<label for="senderPatronymic">-</label>
						<input type="text" class="form-control" id="senderPatronymic" placeholder="- " v-model="name">
						<div class="invalid-feedback">
							 ,   - .
						</div>
					</div>
					<div class="form-group">
						<label for="senderPhone"> </label>
						<input type="text" class="form-control" id="senderPhone" placeholder="+380 (XX) XXX-XX-XX" v-model="amount">
						<div class="invalid-feedback">
							 ,     .
						</div>
					</div>
					<div class="form-group input-disabled">
						<label for="senderBirthday"> </label>
						<input type="text" class="form-control" id="senderBirthday" placeholder=".." disabled>
						<div class="invalid-feedback">
							 ,     .
						</div>
					</div>
					<div class="form-group input-disabled">
						<label for="senderBirthPlace"> </label>
						<input type="text" class="form-control" id="senderBirthPlace" placeholder=" , " disabled>
						<div class="invalid-feedback">
							 ,     .
						</div>
					</div>
					<div class="form-group input-disabled">
						<label for="taxId"></label>
						<input type="text" class="form-control" id="taxId" placeholder=" " disabled>
						<div class="invalid-feedback">
							 ,      .
						</div>
					</div>
				</div>

				<div class="form-section">
					<div class="form-group">
						<label for="typeId"> </label>
						<select class="form-control" id="typeId">
							<option>{{ amount }}</option>
						</select>
						<div class="invalid-feedback">
							 ,     .
						</div>
					</div>

					<div class="form-row">
						<div class="form-group col-md-3">
							<label for="idSeries"></label>
							<input type="text" class="form-control" id="idSeries" placeholder="-" v-model="amount">
							<div class="invalid-feedback">
								 ,    .
							</div>
						</div>
						<div class="form-group col-md-9">
							<label for="numberId"></label>
							<input type="text" class="form-control" id="numberId" placeholder="-" v-model="amount">
							<div class="invalid-feedback">
								 ,    .
							</div>
						</div>
					</div>
					
					<div class="form-group">
						<label for="issueOrganization"> </label>
						<textarea class="form-control" row="3" id="issueOrganization" placeholder=",   "></textarea>	
						<div class="invalid-feedback">
							 ,   ,   .
						</div>
					</div>

					<div class="form-group">
						<label for="issueDate"> </label>
						<input type="text" class="form-control" id="issueDate" placeholder="..">
						<div class="invalid-feedback">
							 ,     .
						</div>
					</div>

				</div>
			</fieldset>
			
			<fieldset class="receiver-data">
				<legend></legend>
				<div class="form-section">
					<div class="form-group">
						<label for="receiverSurname"></label>
						<input type="text" class="form-control" id="receiverSurname" placeholder=" ">
						<div class="invalid-feedback">
							 ,    .
						</div>
					</div>
					<div class="form-group">
						<label for="receiverName">'</label>
						<input type="text" class="form-control" id="receiverName" placeholder="' ">
						<div class="invalid-feedback">
							 ,   ' .
						</div>
					</div>
					<div class="form-group">
						<label for="receiverPatronymic">-</label>
						<input type="text" class="form-control" id="receiverPatronymic" placeholder="- ">
						<div class="invalid-feedback">
							 ,   - .
						</div>
					</div>
					<div class="form-group">
						<label for="receiverPhone"> </label>
						<input type="text" class="form-control" id="receiverPhone" placeholder="+380 (XX) XXX-XX-XX">
						<div class="invalid-feedback">
							 ,     .
						</div>
					</div>
				</div>
			</fieldset>

			<fieldset class="amount-data">
				<legend></legend>
				<div class="form-section">
					<div class="form-group">
						<label for="amount"> , </label>
						<input type="number" class="form-control" id="amount" placeholder="0.00" v-model="amount">
						<div class="invalid-feedback">
							     149 999.99 .
						</div>
					</div>
					<div class="form-group">
						<label for="fee"></label>
						<input type="text" class="form-control" id="fee" value="2%" disabled>
					</div>
					<div class="form-group">
						<label for="totalAmount"> </label>
						<input type="text" class="form-control" id="totalAmount" disabled>
					</div>
					<div class="form-group">
						<label for="amountReceived"> </label>
						<input type="number" class="form-control" id="amountReceived" placeholder="0.00">
						<div class="invalid-feedback">
							        !
						</div>
					</div>
					<div class="form-group">
						<label for="charge"></label>
						<input type="text" class="form-control" id="charge" disabled>
					</div>
				</div>
			</fieldset>
		</form>	
		<div class="form-info">
			<p>  <span class="amount">{{((+amount).toFixed(2)).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1 ")}}</span> UAH</p>
			<button class="btn btn-danger" type="submit" v-on:click="goBack"></button>
		</div>
	</div>
</template>

<script>
import appConfig from '../../main';
import navbar from '@/components/common/navbar';

export default {
	name: 'audit-edit',
	data() {
		return {
			id: '',
			name: '',
			amount: '',
			loading: false
		}
	},
	created() {
		if (!appConfig.audit) {
			this.$router.push('/audits');
		} else {
			this.setData();
		}
	},
	methods: {
		setData() {
			if (appConfig) {
				if (appConfig.audit) {
					this.id = appConfig.audit.id;
					this.name = appConfig.audit.name;
					//this.pass = appConfig.user.pass;
					//this.description = appConfig.user.description;
				}
			}
		},
		goBack() {
			this.$router.push('/audits');
		}
	}
}
</script>



